
html{
  background: #f8f8f8;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
html{
  background-color: #f5f5f5;
}


.pageHeader-wrapper {
  border-bottom: 2px solid red;
  margin-bottom: 50px;
}
.pageHeader {
  display: table;
  max-width: 920px;
  margin: 0 auto;
  height: 70px;
}
.pageHeader__logo-wrapper {
  display: table-cell;
  vertical-align: middle;
  padding-left: 20px;
  width: 90%;
}
.pageHeader__logo-img {
  display: block;
  max-width: 120px;
  height: auto;
}
.pageHeader__lang-wrapper {
  display: table-cell;
  vertical-align: middle;
  padding-left: 30px;
  padding-right: 20px;
  width: auto;
}
.pageHeader__btn-lang {
  float: none;
  display: block;
  cursor: pointer;
  width: 30px;
  height: 30px;
  margin-left: auto;
  margin-right: 0;
  margin-top: 0;
}
.pageHeader__btn-lang-img {
  display: block;
  width: 100%;
  height: 100%;
}


.theFooter{
   max-width: 920px;
   margin: 0 auto;
}
.page_bottom_remark {
  text-align: left;
}
.page_bottom_remark .content-block {
  text-align: left;
  padding: 26px 0px;
  font-size: 12px;
  margin-bottom: 0;
}
.pageCopyright {
  margin: 0;
  padding: 20px 18px;
  font-size: 12px;
  color: #333333;
  letter-spacing: -0.21px;
}


.storckTab_wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  max-width: 920px;
  line-height: 40px;
  margin: 0 auto;
  overflow: auto;
}
.stockTabCombo_wrapper {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  padding-bottom: 40px;
}
.stockTabCombo_wrapper .stockTabCombo_item> a {
  font-size: 14px;
  text-decoration: none;
  color: rgb(77, 85, 93);
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
}
.zzjs {
    display: block;
    width: 1px;
    height: 1px;
    border: solid 16px;
    border-color: transparent transparent #ff0000 transparent;
    position: absolute;
    overflow: hidden;
    z-index: 1;
    left: 35%;
    top: 125%;
}
.stockTabCombo_wrapper .stockTabCombo_item> .active {
  color: rgb(240, 20, 20);
  border: 1px solid rgb(255, 0, 0);
}
.selectButtons_span{
  padding: 10px 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
}



#store_area_table>tr>td {
  padding: 30px;
}
#store_area_table>tr:nth-child(even) {
  background: #f3f3f3;
}
.view_map_button,.storeaddDetail {
  color: #9c9c9c;
  font-size: 13px;
  cursor: pointer;
}
.view_map_button .material-icons {
  color: rgb(255,0,0);
  font-size: 15px;
  padding-right: 5px;
  vertical-align: top;
}

#detailPage{
    max-width: 920px;
    margin: 0 auto;
    background: #ffffff;
    -webkit-box-shadow: 3px 3px 5px #cccccc;
            box-shadow: 3px 3px 5px #cccccc;
    /* margin-top: 40px; */
    border-top: 2px solid red;
}
.storeDetail {
   
    -webkit-box-shadow: 2px 2px 20px #cccccc;
   
            box-shadow: 2px 2px 20px #cccccc;;
    max-width: 1080px;
    margin: 40px auto;
    margin-top: 25px;
}
.content-topSide {
      padding: 30px 30px 0 30px;
}
.content-topSide_store-phone-availability_icon {
    max-width: 500px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin: 0 auto;
    padding-bottom: 10px;
    font-size: 14px;
}
.store-phone-availability_icon_item {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 0px;
            flex: 1 0 0;
}
.material-icons {
    vertical-align: middle;
    font-weight: bold;
    font-size: 20px;
}
.content-topSide_store-phone-availability_icon span {
    font-size: 12px;
    padding:  0 15px 0 5px;
}
.content-topSide_store-phone-availability_wrapper {
    padding: 0 30px;
}
.content-topSide_store-phone-availability_content {
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    max-width: 920px;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.content-topSide_store-phone-availability_location {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: left;
    margin-top: 20px;
}
.content-topSide_store-phone-availability_content ul{
    padding-left: 0px;
    margin-bottom: 0;
}
.content-topSide_store-phone-availability_location .location_item {
    display: inline-block;
    list-style: none;
    padding: 10px 15px 10px 0px;
    cursor: pointer;
}
.target_link {
    position: relative;
    text-decoration: none;
    margin-right: 15px;
    font-size: 14px;
}
a.router-link-exact-active.active {
    color: red;
    padding-bottom: 10px;
    border-bottom: 3px solid red;
    z-index: 10;
    font-weight: bold;
}
.content-topSide_store-phone-availability_color {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    text-align: right;
    padding-left: 36px;
}
.content-topSide_store-phone-availability_color .color_item {
     display: inline-block;
    list-style: none;
    padding: 7px 16px;
    font-size: 12px;
    text-align: center;
}
.split_line {
    position: relative;
    border:1px solid #ccc;
}
.split_line_position {
    position: absolute;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    right: 0;
    top: -2px;
}
.split_line_short {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    border: 1px solid rgb(119, 119, 119);
    height: 4px;
    width: 3.5rem;
    margin-right: 5px;
    display: inline-table;
    margin-right: 6px;
}
.gold {
    right: 0;
    background: rgb(255, 216, 189);
}
.silver {
    right: 6%;
    background: #D8D8D8;
}
.gray {
    right: 13%;
    background: #000000;
}
.store_area_table_content {
    max-width: 920px;
    margin: 0 auto;
}
.store_area_table_content table {
    text-align: left;
    width: 100%;
}
.store_area_table_content table td {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.store_area_table_item {
    -webkit-box-flex: 1;
        -ms-flex: 1 0 0px;
            flex: 1 0 0;
    margin: 0 auto;
}
.store_area_table_item:last-child {
   text-align: right;
}
.store_area_table_icon_item {
    display: inline-block;
    padding: 22px;
}
.store_area_table_icon_item .material-icons {
    font-size: 18px;
}
